<template>
  <div>
    <!-- .header>login-btn>img+span.text -->
    <!-- 未登录 -->
    <div class="header not-login" v-if="!$store.state.token">
      <div class="login-btn" @click="$router.push('/login')">
        <img src="../../assets/mobile.png" alt="" />
        <span class="text">登录/注册</span>
      </div>
    </div>
    <!-- 已登录 -->
    <div class="header login" v-else>
      <div class="base-info">
        <div class="left">
          <van-image
            class="avatar"
            :src="userInfo.photo"
            round
            fit="cover"
          />
          <!-- <van-image class="avatar" src="../../assets/微信图片_20220321094433.jpg" round fit="cover" /> -->
          <span class="name">{{userInfo.name}}</span>
        </div>
        <div class="right">
          <van-button size="mini" round @click="$router.push('/user')">编辑个人资料</van-button>
        </div>
      </div>
      <div class="data-stats">
        <div class="data-item">
          <span class="count">{{userInfo.art_count}}</span>
          <span class="text">头条</span>
        </div>
        <div class="data-item">
          <span class="count">{{userInfo.fans_count}}</span>
          <span class="text">关注</span>
        </div>
        <div class="data-item">
          <span class="count">{{userInfo.follow_count}}</span>
          <span class="text">粉丝</span>
        </div>
        <div class="data-item">
          <span class="count">{{userInfo.like_count}}</span>
          <span class="text">获赞</span>
        </div>
      </div>
    </div>

    <!-- 宫格导航 -->
    <van-grid :column-num="2">
      <van-grid-item>
        <i slot="icon" class="toutiao toutiao-shoucang"></i>
        <span slot="text" class="text">收藏</span>
      </van-grid-item>
      <van-grid-item>
        <i slot="icon" class="toutiao toutiao-lishi"></i>
        <span slot="text" class="text">历史</span>
      </van-grid-item>
    </van-grid>
    <!-- /宫格导航 -->

    <van-cell title="消息通知" is-link class="mt-10"/>
    <van-cell class="mb-9" title="小智同学" is-link />
    <van-cell
      v-if="$store.state.token"
      class="mt-10 logout"
      clickable
      title="退出登录"
      @click="onLogout"
    />
  </div>
</template>

<script>
import { getUserInfo } from '@/api/user.js'
export default {
  data () {
    return {
      userInfo: {}
    }
  },
  components: {},
  async created () {
    const res = await getUserInfo()
    this.userInfo = res.data.data
  },
  methods: {
    onLogout () {
      this.$dialog.confirm({
        title: '提示',
        message: '确认退出?'
      })
        .then(() => {
        // on confirm
        // 清空 token
          this.$store.commit('removeToken')
        }).catch(() => {
        // on cancel
        })
    }
  }
}
</script>

<style lang="less" scoped>
.header {
  height: 361px;
  background-image: url("../../assets/banner.png");
  background-size: cover;
  // display: flex;
  // justify-content: center;
  // align-items: center;

  &.not-login {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
  }

  .login-btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .mobile-img {
    width: 132px;
    height: 132px;
    margin-bottom: 15px;
  }

  .text {
    font-size: 28px;
    color: #fff;
  }
}

.base-info {
  height: 231px;
  padding: 76px 32px 23px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .left {
    display: flex;
    align-items: center;
  }
  .avatar {
    width: 132px;
    height: 132px;
    border: 4px solid #fff;
    margin-right: 23px;
  }
}
.data-stats {
  display: flex;
  .data-item {
    height: 130px;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    .count {
      font-size: 36px;
    }
    .text {
      font-size: 23px;
    }
  }
}
.van-grid-item {
  i.toutiao {
    font-size: 45px;
  }
  .toutiao-shoucang {
    color: #eb5253;
  }
  .toutiao-lishi {
    color: #ff9d1d;
  }
  span.text {
    font-size: 28px;
  }
}
.mt-10{
  margin-top:10px ;
}
.logout{
  text-align: center;
  color:#d86262;
}
</style>
